___CSS_LOADER_EXPORT___.push([module.id, `/* Credits: */
/* 3D tower loader made by: csozi | Website: www.csozi.hu*/

.loader {
    scale: 3;
    height: 50px;
    width: 40px;
  }
  
  .box {
    position: relative;
    opacity: 0;
    left: 10px;
  }
  
  .side-left {
    position: absolute;
    /* background-color: #286cb5; */
    width: 19px;
    height: 5px;
    transform: skew(0deg, -25deg);
    top: 14px;
    left: 10px;
  }
  
  .side-right {
    position: absolute;
    /* background-color: #2f85e0; */
    width: 19px;
    height: 5px;
    transform: skew(0deg, 25deg);
    top: 14px;
    left: -9px;
  }
  
  .side-top {
    position: absolute;
    /* background-color: #5fa8f5; */
    width: 20px;
    height: 20px;
    rotate: 45deg;
    transform: skew(-20deg, -20deg);
  }
  
  .box-1 {
    animation: from-left 4s infinite;
  }
  
  .box-2 {
    animation: from-right 4s infinite;
    animation-delay: 1s;
  }
  
  .box-3 {
    animation: from-left 4s infinite;
    animation-delay: 2s;
  }
  
  .box-4 {
    animation: from-right 4s infinite;
    animation-delay: 3s;
  }
  
  @keyframes from-left {
    0% {
      z-index: 20;
      opacity: 0;
      translate: -20px -6px;
    }
  
    20% {
      z-index: 10;
      opacity: 1;
      translate: 0px 0px;
    }
  
    40% {
      z-index: 9;
      translate: 0px 4px;
    }
  
    60% {
      z-index: 8;
      translate: 0px 8px;
    }
  
    80% {
      z-index: 7;
      opacity: 1;
      translate: 0px 12px;
    }
  
    100% {
      z-index: 5;
      translate: 0px 30px;
      opacity: 0;
    }
  }
  
  @keyframes from-right {
    0% {
      z-index: 20;
      opacity: 0;
      translate: 20px -6px;
    }
  
    20% {
      z-index: 10;
      opacity: 1;
      translate: 0px 0px;
    }
  
    40% {
      z-index: 9;
      translate: 0px 4px;
    }
  
    60% {
      z-index: 8;
      translate: 0px 8px;
    }
  
    80% {
      z-index: 7;
      opacity: 1;
      translate: 0px 12px;
    }
  
    100% {
      z-index: 5;
      translate: 0px 30px;
      opacity: 0;
    }
  }`, "",{"version":3,"sources":["webpack://./src/Components/Loaders/StackLoader/style.css"],"names":[],"mappings":"AAAA,aAAa;AACb,0DAA0D;;AAE1D;IACI,QAAQ;IACR,YAAY;IACZ,WAAW;EACb;;EAEA;IACE,kBAAkB;IAClB,UAAU;IACV,UAAU;EACZ;;EAEA;IACE,kBAAkB;IAClB,+BAA+B;IAC/B,WAAW;IACX,WAAW;IACX,6BAA6B;IAC7B,SAAS;IACT,UAAU;EACZ;;EAEA;IACE,kBAAkB;IAClB,+BAA+B;IAC/B,WAAW;IACX,WAAW;IACX,4BAA4B;IAC5B,SAAS;IACT,UAAU;EACZ;;EAEA;IACE,kBAAkB;IAClB,+BAA+B;IAC/B,WAAW;IACX,YAAY;IACZ,aAAa;IACb,+BAA+B;EACjC;;EAEA;IACE,gCAAgC;EAClC;;EAEA;IACE,iCAAiC;IACjC,mBAAmB;EACrB;;EAEA;IACE,gCAAgC;IAChC,mBAAmB;EACrB;;EAEA;IACE,iCAAiC;IACjC,mBAAmB;EACrB;;EAEA;IACE;MACE,WAAW;MACX,UAAU;MACV,qBAAqB;IACvB;;IAEA;MACE,WAAW;MACX,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,UAAU;MACV,mBAAmB;IACrB;;IAEA;MACE,UAAU;MACV,mBAAmB;MACnB,UAAU;IACZ;EACF;;EAEA;IACE;MACE,WAAW;MACX,UAAU;MACV,oBAAoB;IACtB;;IAEA;MACE,WAAW;MACX,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,UAAU;MACV,mBAAmB;IACrB;;IAEA;MACE,UAAU;MACV,mBAAmB;MACnB,UAAU;IACZ;EACF","sourcesContent":["/* Credits: */\r\n/* 3D tower loader made by: csozi | Website: www.csozi.hu*/\r\n\r\n.loader {\r\n    scale: 3;\r\n    height: 50px;\r\n    width: 40px;\r\n  }\r\n  \r\n  .box {\r\n    position: relative;\r\n    opacity: 0;\r\n    left: 10px;\r\n  }\r\n  \r\n  .side-left {\r\n    position: absolute;\r\n    /* background-color: #286cb5; */\r\n    width: 19px;\r\n    height: 5px;\r\n    transform: skew(0deg, -25deg);\r\n    top: 14px;\r\n    left: 10px;\r\n  }\r\n  \r\n  .side-right {\r\n    position: absolute;\r\n    /* background-color: #2f85e0; */\r\n    width: 19px;\r\n    height: 5px;\r\n    transform: skew(0deg, 25deg);\r\n    top: 14px;\r\n    left: -9px;\r\n  }\r\n  \r\n  .side-top {\r\n    position: absolute;\r\n    /* background-color: #5fa8f5; */\r\n    width: 20px;\r\n    height: 20px;\r\n    rotate: 45deg;\r\n    transform: skew(-